import React, { useRef, useState } from 'react';
import database from '../database/firebase';
import { Input, Button, Modal, Container, CssBaseline, makeStyles, Avatar, TextField, Typography, Box, Switch, TextareaAutosize } from '@material-ui/core';
import EditOutlined from '@material-ui/icons/EditOutlined';
import DeleteOutlined from '@material-ui/icons/DeleteOutlined';
import DateRangeOutlined from '@material-ui/icons/DateRangeOutlined';
import AddAlertOutlined from '@material-ui/icons/AddAlertOutlined';


const useStyles = makeStyles((theme) => ({
    form: {
        width: '70%', // Fix IE 11 issue.
        margin: '0 auto',
        padding: '50px',
        textAlign: 'center',
        justifyContent: 'space-around',
        display: 'flex'
    }
}));


function Todo(prop) {

    console.log(prop.todo);
    const classes = useStyles();
    const [open, setOpen] = useState(false);
    const [updateTask, setUpdateTask] = useState(prop.todo);
    const [desc, setDesc] = useState(prop.description);
    const [priority, setPriority] = useState(prop.priority);
    const dueDateRef = useRef(prop.duedate);
    //Edit Task Functionality
    function handleTaskEdit(e) {
        e.preventDefault();
        setOpen(true);
    }
    const handlePriority = (event)=>{
        return setPriority(!priority);
      }
      
    function handleTaskUpdate(e) {
        e.preventDefault();
        database.collection('todo').doc(prop.id).set({
            "todo": updateTask,
            "description": desc,
            "priority": priority,
            "duedate": dueDateRef.current.value
          }, { merge: true }).then(()=>{
              setOpen(false);
          }).catch((err)=>{
            console.log(err);
          });
    }
    return (
        <>
            <Modal
                className="app__todo__modal"
                open={open}
                onClose={() => { setOpen(false) }}
            >
                {
                    <div className="">
                        <form className="app__todo__form">
                            <span className="atp__span atp__span__title">
                                <span className="atp__title__label">Task Title</span>
                                <Input className="atp__title" value={updateTask} onChange={(e) => setUpdateTask(e.target.value)} />
                            </span>
                            <span className="atp__span atp__span__desc">
                            <span className="atp__title__label">Task Description</span>
                                <TextareaAutosize className="atp__desc" value={desc} onChange={e => setDesc(e.target.value)} />
                            </span>
                            <span className="atp__span atp__span__date">
                                <span className="atp__title__label">Due Date</span>
                                <TextField
                                    inputRef={dueDateRef}
                                    id="date"
                                    defaultValue={prop.duedate}
                                    type="date"
                                    className="atp__dueDate"
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                />
                            </span>


                            <span className="atp__span atp__span__priority">
                                <span className="atp__title__label">Priority</span>
                                <Switch
                                    checked={priority}
                                    onChange={handlePriority}
                                    name="priority"
                                    inputProps={{ 'aria-label': 'secondary checkbox' }}
                                />

                            </span>

                            <Button disabled={!updateTask} variant="contained" color="primary" onClick={handleTaskUpdate}>Update</Button>
                        </form>
                    </div>
                }
            </Modal>
            <li className="todo__li" id={`todo__li__${prop.id}`}>
                <span className="todo__li__span" >{prop.todo}</span>
                {/* <span className="todo__li__span" >{prop.description}</span> */}
                
                <span className="date__prio__main">
                <span className=" date__prio" ><DateRangeOutlined className="todo__icon" />{prop.duedate ? <span>{prop.duedate}</span>:<span>No Due Date</span>}</span>
                <span className="todo__urgent" >{prop.priority ? <span className="urgent"><AddAlertOutlined className="todo__icon" color="secondary"/>Urgent</span> : <span></span>} </span>
                </span>

                <span className="edit__delete__main">
                <span className="todo__edit"><Button onClick={handleTaskEdit}><EditOutlined className="todo__icon__edit" color="primary" /></Button></span>
                <span className="todo__delete"><Button onClick={(event) => {
                    database.collection('todo').doc(prop.id).delete()
                }}><DeleteOutlined className="todo__icon__delete" color="secondary" /></Button></span>
                </span>
            </li>
        </>
    );
}
export default Todo;