import React, { useEffect, useRef, useState } from 'react';
import database from './database/firebase';
import firebase from 'firebase';
import Todo from './component/Todo';
import { Input, Button, Modal, TextareaAutosize, TextField, Switch } from '@material-ui/core';
import './App.css';
function App() {
  const [task, setTask] = useState([]);
  const [input, setInput] = useState('');
  const [desc, setDesc] = useState('');
  const [priority, setPriority] = useState(false);
  const dueDateRef = useRef();
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    database.collection('todo').orderBy('timestamps', 'desc').onSnapshot((snapshot) => {
      setTask(snapshot.docs.map((doc) => { return { "data": doc.data(), "id": doc.id }; }));
    })

  }, []);

  const handlePriority = (event)=>{
    return setPriority(!priority);
  }
  
  const handleTodo = (e) => {
    e.preventDefault();
    setOpen(false);
    // Add the task Data to the List
    /*     setTask([...task, input]);
    */
   // Add data to the firebase collections
   
   database.collection('todo').add({
     "todo": input,
     "timestamps": firebase.firestore.FieldValue.serverTimestamp(),
      "description": desc,
      "priority": priority,
      "duedate": dueDateRef.current.value
    }, { merge: true }).then(()=>{
      
      setInput('');
      setDesc('');
      dueDateRef.current.value= '';
      setPriority(false);
      setLoading(true);
      
    }).catch((err)=>{
      console.log(err);
    });
  }
  return (
    <div className="App">
      <header className="App-header">

        <h1>
          Todo App With ReactJs
        </h1>
        {/* 
            Creating a todo app
            
            -- adding form consisting a text box and a button
            -- on button click the text should get added to the list 
            -- display all the todo list data (if any)
            -- edit and delete functionality for each list data 
            
            -- Designing with Material Ui 
            -- Integrate firebase
            
          */}
      </header>
      <div className="todo__main__div">
        <div className="todo__Form">

          {/* 
            Add A button to Open a Modal
            Modal will have
            -- Title Textbox
            -- Decription Textbox
            -- Priority 
            -- Due Date 
          */}
          <Modal className="app__todo__modal" open={open} onClose={() => setOpen(!open)}>

            <form className="app__todo__form">

              <span className="atp__span atp__span__title">
                <span className="atp__title__label">Task Title</span>
                <Input className="atp__title" value={input} onChange={e => setInput(e.target.value)} />
              </span>
              <span className="atp__span atp__span__desc">

                <span className="atp__title__label">Task Description</span>
                <TextareaAutosize className="atp__desc" value={desc} onChange={e => setDesc(e.target.value)} />
              </span>


              <span className="atp__span atp__span__date">
                <span className="atp__title__label">Due Date</span>
                <TextField
                  inputRef={dueDateRef}
                  id="date"
                  type="date"
                  className="atp__dueDate"
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
              </span>


              <span className="atp__span atp__span__priority">
                <span className="atp__title__label">Priority</span>
                <Switch
                  checked={priority}
                  onChange={handlePriority}
                  name="priority"
                  inputProps={{ 'aria-label': 'secondary checkbox' }}
                />

              </span>

              <Button disabled={!input && loading} variant="contained" onClick={handleTodo}>Add Todo</Button>
            </form>

          </Modal>

          <Button variant="contained" onClick={() => setOpen(true)}>Create Todo</Button>
        </div>
        <ul className="todo__ul">
          {/* Create A Separate COmponent FOr Looping Through List */}
          {task.map((t, index) => {
            return <Todo {...t.data} id={t.id} key={index} />
          })}
        </ul>
      </div>

    </div>
  );
}

export default App;
